export default{
	data(){
		return{
			pickup:'',
			pickupstate:false,
			id:'',
			windowHeight:'',
		}
	},
	onLoad() {
		var that=this;
		that.windowHeight = this.statusBarHeight+44;
		uni.getStorage({
			key:'userinfo',
			success:function(res) {
				that.id=res.data.id;
				that.getinfo();
			}
		})
	},
	methods: {
		switch1Change:function(e){
			var that=this;
			if(e.target.value== false){
				that.pickup=0;
				that.pickupstate =false;
			}else{
				that.pickup=1;
				that.pickupstate =true;
			}
			console.log(that.pickup);
		},
		back:function(){
			uni.navigateBack({
				delta: 1
			});
		},
		getinfo:function(){
			var that=this;
			uni.request({
				url: that.serveipd+'/api/merchant/auth/info/'+that.id,
				method: 'GET',
				data: {
					id:that.id
				},
				success: res => {
					that.pickup=res.data.data.pickUp;
					console.log(res.data.data.pickUp);
					if(that.pickup == '0'){
						that.pickupstate =false;
					}else{
						that.pickupstate =true;
					}
				},
				fail: () => {},
				complete: () => {}
			});
		}
	},
}
